.btn-open-nav.is-active span {
  border-color: #fff;
}

.btn-open-nav.is-active span:nth-child(1) {
  transform: rotate(45deg) translate3d(6px, -3px, 0);
}

.btn-open-nav.is-active span:nth-child(2) {
  opacity: 0;
  transform: scaleX(0);
}

.btn-open-nav.is-active span:nth-child(3) {
