.is-split [data-stagger="wrap"] {
  overflow: hidden;
  display: block;
}

[data-stagger="wrap"].is-clear-transform {
  overflow: initial;
  display: initial;
}

.is-split [data-stagger="inner"] {
  position: relative;
  transform: translateY(100%);
  opacity: 0;
  display: block;
}

[data-stagger="inner"].is-clear-transform {
  position: initial;
  transform: initial;
  opacity: initial;
  display: initial;
}

[data-anim-from] {
  position: relative;
  opacity: 0;
}

[data-anim-from].is-clear-transform {
  transform: initial;
  opacity: initial;
}

[data-anim-from="top"] {
  transform: translateY(-50px);
}

[data-anim-from="top-10"] {
  transform: translateY(-10px);
}

[data-anim-from="left"] {
  transform: translateX(-50px);
}

[data-anim-from="right-20"] {
  transform: translateX(20px);
}

[data-anim-page="full"] {
  position: relative;
  transform: translateY(100vh);
}

[data-anim-page="full"].is-clear-transform {
  transform: initial;
}
